.gpw .trigger {
    padding: 5px;
    background: rgb(255, 255, 255);
    border-radius: 1px;
    box-shadow: rgba(0, 0, 0, 0.1) 0 0 0 1px;
    display: inline-block;
    cursor: pointer;
}

.gpw .trigger .inner {
    width: 36px;
    height: 14px;
    border-radius: 2px;
}

