        rgba(0, 0, 0, 0.15) 0 8px 16px;
    background-color: white;
    padding: 12px;
    border-radius: 4px;
    position: absolute;
}

.gpw .overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
